  height: 50px;
  border: 1px solid rgba(72, 73, 77, 0.2);
  background-color: #ffffff;
  padding: 15px 25px;
  transition: all 0.5s; }
  .popup__input::placeholder {
    font-size: 14px;
    line-height: 19px; }
  .popup__input:hover, .popup__input:focus {
    border: 1px solid rgba(72, 73, 77, 0.5);
    outline: none;
    transition: all 0.5s; }

.popup__rate {
  margin-top: 10px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between; }

.popup__rating {
  display: inline-flex;
  flex-direction: row-reverse; }

.popup__star {
  display: none; }
  .popup__star + label {
    display: inline-block;
    position: relative;
    margin-right: 8px;
    width: 27px;
    height: 15px;
    cursor: pointer; }
    .popup__star + label:first-child {
      margin-right: 0; }

.popup__star + label:hover ~ label > .star_gray,
.popup__star + label:hover .star_gray {
  opacity: 0; }

.popup__star + label:hover ~ label > .star_red,
.popup__star + label:hover .star_red {
  opacity: 1; }

.popup__star:checked + label ~ label > .star_gray,
.popup__star:checked + label > .star_gray {
  opacity: 0; }

.popup__star:checked + label ~ label > .star_red,
.popup__star:checked + label > .star_red {
  opacity: 1; }

.star {
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.5s; }

.star_red {
  opacity: 0; }

.popup__rate p {
  display: inline-block;
  font-weight: 600;
  font-size: 15px;
  line-height: 20px;
  color: #000000; }

.popup__textarea {
  width: 300px;
